





import Vue from "vue";
// svgimport
const importAll = (requireContext: __WebpackModuleApi.RequireContext) =>
  requireContext.keys().forEach(requireContext);
try {
  importAll(require.context("../assets/icon", true, /\.svg$/));
} catch (e) {
  console.log(e);
}

export default Vue.extend({
  props: ["name"],
});
