import Vue from "vue";
import Vuex, { Store } from "vuex";
import createID from "@/lib/createID.ts";
import router from "@/router";

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    // 
    tagList: [] as Tag[],
    // 
    recordList: [] as RecordItem[],
    record: {
      selectedTag: {
        id: "0",
        name: "",
        type: "-",
        iconName: "catering",
      },
      note: "",
      type: "-",
      amount: "0",
    },
  },
  mutations: {
    // 
    getRecordList(state) {
      state.recordList = JSON.parse(
        window.localStorage.getItem("recordList") || "[]"
      );
    },
    // 
    editRecordList(state, newRecord) {
      state.recordList.push(newRecord);
      window.localStorage.setItem(
        "recordList",
        JSON.stringify(state.recordList)
      );
      router.push("/item");
    },
    //idrecord
    getRecordByID(state, ID) {
      return state.recordList.filter((i) => i.id === ID);
    },
    // 
    getTagList(state) {
      state.tagList = JSON.parse(
        window.localStorage.getItem("tagList") || "[]"
      );
      if (state.tagList.length === 0) {
        (state.tagList = [
          { id: "0", name: "", type: "-", iconName: "catering" },
          { id: "1", name: "", type: "-", iconName: "traffic" },
          { id: "2", name: "", type: "-", iconName: "medical" },
          { id: "3", name: "", type: "-", iconName: "commodity" },
          { id: "4", name: "", type: "-", iconName: "amusement" },
          { id: "5", name: "", type: "-", iconName: "digital" },
          { id: "6", name: "", type: "-", iconName: "home" },
          { id: "7", name: "", type: "+", iconName: "salary" },
          { id: "8", name: "", type: "+", iconName: "bonus" },
          { id: "9", name: "", type: "+", iconName: "subsidy" },
          { id: "10", name: "", type: "+", iconName: "invest" },
          { id: "11", name: "", type: "+", iconName: "part-time" },
        ]),
          store.commit("saveTagList");
      }
    },
    // localStorage
    saveTagList(state) {
      window.localStorage.setItem("tagList", JSON.stringify(state.tagList));
    },
    // 
    deleteRecord(state, ID) {
      const index = state.recordList.findIndex((i) => i.id === ID);
      if (index >= 0) {
        state.recordList.splice(index, 1);
        window.localStorage.setItem(
          "recordList",
          JSON.stringify(state.recordList)
        );
        router.push("/item");
      } else {
        window.alert("");
      }
    },
    // 
    createTag(state) {
      const newTagName = window.prompt("") as string;
      const names: string[] = state.tagList.map((item: Tag) => item.name);
      if (newTagName === null) {
        return;
      }
      if (newTagName.trim() === "" || newTagName === "") {
        window.alert("");
      } else if (names.indexOf(newTagName) >= 0) {
        window.alert("");
      } else {
        const tagID = createID();
        const iconN = state.record.type === "-" ? "output" : "income";
        state.tagList.push({
          id: tagID,
          name: newTagName,
          type: state.record.type,
          iconName: iconN,
        });
        store.commit("saveTagList");
      }
    },
    // ---
    editTag(state, newTag) {
      const names = state.tagList.map((item) => item.name);
      if (newTag.name === "" || newTag.name.trim() === "") {
        window.alert("");
      } else if (names.indexOf(newTag.name) >= 0) {
        window.alert("");
      } else {
        const index = state.tagList.findIndex((item) => item.id === newTag.id);
        state.tagList.splice(index, 1, newTag);
        store.commit("saveTagList");
        window.alert("");
      }
    },
    // --
    deleteTag(state, newTag) {
      const index = state.tagList.findIndex((item) => item.id === newTag.id);
      if (index >= 0) {
        state.tagList.splice(index, 1);
        store.commit("saveTagList");
        router.push("/labels");
      } else {
        window.alert("");
      }
    },
    // Tabsrecord
    toggleRecord(state) {
      if (state.record.type === "-") {
        state.record = {
          selectedTag: {
            id: "0",
            name: "",
            type: "-",
            iconName: "catering",
          },
          note: "",
          type: "-",
          amount: "0",
        };
      } else if (state.record.type === "+") {
        state.record = {
          selectedTag: {
            id: "7",
            name: "",
            type: "+",
            iconName: "salary",
          },
          note: "",
          type: "+",
          amount: "0",
        };
      } else {
        window.alert("");
      }
    },
    // record
    initialRecord(state) {
      state.record = {
        selectedTag: {
          id: "0",
          name: "",
          type: "-",
          iconName: "catering",
        },
        note: "",
        type: "-",
        amount: "0",
      };
    },
  },
});

export default store;
