





















import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component
export default class NumberPad extends Vue {
  @Prop(String) readonly amount!: string;
  // 0
  output = "0";
  // 
  inputContent(event: MouseEvent) {
    // button
    const inputValue = (event.target as HTMLButtonElement).textContent;
    // 16
    if (this.output.length < 16) {
      if (this.output.indexOf(".") >= 0 && inputValue === ".") {
        return;
      }
      if (this.output === "0" && inputValue !== ".") {
        this.output = inputValue as string;
      } else {
        this.output += inputValue;
      }
    }
  }
  // 
  backWord() {
    if (this.output.length > 1) {
      this.output = this.output.slice(0, -1);
    } else {
      this.output = "0";
    }
  }
  // 
  clear() {
    this.output = "0";
  }
  //
  ok() {
    if (this.output === "0" || this.output === "0.") {
      window.alert("");
    } else {
      this.$emit("update:amount", this.output);
      this.$emit("submit", this.output);
      this.output = "0";
    }
  }
}
